<template>
  <div class="container">
    <menuAdmin></menuAdmin>
    <div class="spinner-border" role="status" v-if="loading === true">
      <span class="sr-only">cargando...</span>
    </div>
    <div class="row">
      <div class="col-12">
        <div class="custom-control custom-switch" @click="updateChat('chat')">
          <input type="checkbox" id="chat" class="custom-control-input" :checked="chat" />
          <label
            class="custom-control-label"
            :class="{ opacity: chat == false }"
            >Mostrar Chat</label
          >
        </div>
      </div>
      <div class="col-12 mt-5">
        <div class="custom-control custom-switch" @click="updateContador()">
          <input type="checkbox" id="contador" class="custom-control-input" :checked="contador" />
          <label
            class="custom-control-label"
            :class="{ opacity: contador == false }"
            >Contador</label
          >         
        </div>
      </div>
      <div class="col-12">
         <input
            type="datetime-local"
            class="form-control video"
            id="fecha"
            placeholder="ingresa la fecha"
            required
            v-model="fecha"
          />
          <button
            type="submit"
            class="btn btn-primary submit"
            @click="updateFecha()"
            :disabled="contador==false"
          >
            Guardar
          </button>
      </div>    
      <div class="col-12 mt-5">
        <div class="form-group">
          <label for="titulo">Titulo de página</label>
          <input
            type="text"
            class="form-control input"
            id="titulo"
            placeholder="debes ingresar una titulo"
            required
            v-model="title"
          />
          <button
            type="submit"
            class="btn btn-primary submit"
            @click="updateTitle()"
            :disabled="title.length == 0"
          >
            Guardar
          </button>
        </div>
      </div>
      <div class="col-12 mt-5">
        <div class="form-group">
          <label for="exampleInputEmail1">Url iframe video</label>
          <input
            type="url"
            class="form-control video"
            id="video"
            placeholder="debes ingresar una url"
            required
            v-model="videoUrl"
          />
          <button
            type="submit"
            class="btn btn-primary submit"
            @click="updateVideo()"
            :disabled="videoUrl.length == 0"
          >
            Guardar
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import fire from "./../fire";
import menuAdmin from "../components/MenuAdmin";
export default {
  name: "AdminConfig",
  components: { menuAdmin },
  // eslint-disable-next-line vue/no-unused-components
  data() {
    return {
      messages: [],
      loading: true,
      chat: false,
      videoUrl: "",
      title: "",
      fecha: '',
      contador: false
    };
  },
  methods: {
    updateChat(id) {
      let checkBox = document.getElementById(id);
      if (checkBox.checked == true){
        this.chat = false;
      } else {
        this.chat = true;
      }
      fire
        .database()
        .ref("config")
        .update(
          {
            chat: this.chat
          },
          function(error) {
            if (error) {
              console.log("The write failed");
            } else {
              console.log("Data saved successfully!");
            }
          }
        );
    },
    updateVideo() {
      if (this.videoUrl.length > 0) {
        fire
          .database()
          .ref("config")
          .update(
            {
              video: this.videoUrl
            },
            function(error) {
              if (error) {
                console.log("The write failed");
              } else {
                console.log("Data saved successfully!");
              }
            }
          );
      }
    },
    updateTitle() {
      if (this.title.length > 0) {
        fire
          .database()
          .ref("config")
          .update(
            {
              titulo: this.title
            },
            function(error) {
              if (error) {
                console.log("The write failed");
              } else {
                console.log("Data saved successfully!");
              }
            }
          );
      }
    },
    updateContador() {
      let checkBox = document.getElementById('contador');
      if (checkBox.checked == true){
        this.contador = false;
      } else {
        this.contador = true;
      }
      fire
        .database()
        .ref("config")
        .update(
          {
            contador: this.contador
          },
          function(error) {
            if (error) {
              console.log("The write failed");
            } else {
              console.log("Data saved successfully!");
            }
          }
        );
    },
    updateFecha() {
      if (this.fecha.length > 0) {
        fire
          .database()
          .ref("config")
          .update(
            {
              fecha: this.fecha
            },
            function(error) {
              if (error) {
                console.log("The write failed");
              } else {
                console.log("Data saved successfully!");
              }
            }
          );
      }
    }
  },
  mounted() {
    let ref = this;
    const itemsRef = fire.database().ref("config");
    console.log(itemsRef);
    itemsRef.on("value", snapshot => {
      ref.loading = false;
      let data = snapshot.val();
      console.log(data);
      if (data.chat === true) {
        ref.chat = true;
      } else {
        ref.chat = false;
      }
      ref.videoUrl = data.video;
      ref.title = data.titulo;
      ref.contador = data.contador;
      if(data.fecha){
        ref.fecha = data.fecha;
      }
    });
  }
};
</script>

<style lang="scss">
.input{
  
}
.submit{
  margin-top: 10px;
}
</style>
