<template>
  <div class="login">
    <!-- Chat section -->
    <div class="spinner-border" role="status" v-if="loading === true">
      <span class="sr-only">cargando...</span>
    </div>

    <div v-else>
      <div class="message-body" id="mensajes">
        <div class="">
          <div class="card-body">
            <div
              class="pl-2 pt-1 message-text"
              :class="{
                me: name == message.username,
                last: index === messages.length - 1
              }"
              v-for="(message, index) in messages"
              :key="message.id"
            >
              <p class="message pt-1">
                <span class="mg-text text-capitalize">
                  {{ message.username }} : &nbsp;
                </span>
                {{ message.text }}
              </p>
              <!--
              <div class="fecha">
                <small>{{ $moment(message.fecha).fromNow() }}</small>
              </div>
              -->
            </div>
            <hr id="fin" />
          </div>
          <hr id="fin"/>
        </div>
      </div>
      <!-- Login section -->
      <div class="box-post" v-if="!name">
        <input
          class="form-control-sm"
          type="text"
          v-model="userName"
          placeholder="Ingresa tu nombre"
          @keyup="nombreUsado = false"
          maxlength="100"
        />
        <button class="btn" @click="updateUsername">Ingresar</button>
        <div class="alerta" v-if="nombreUsado === true && userName.length > 0">
          El usuario <strong>{{ userName }}</strong> ya está en uso, escoja otro nombre.
        </div>
      </div>

      <div class="box-post">
        <textarea
          class="form-control-sm"
          v-model="mensaje"
          placeholder="Escribe tu comentario"
          :disabled="!name"
        ></textarea>
        <button class="btn enviar" @click="enviarMensaje" :disabled="!name">
          Enviar
        </button>
        <div v-if="spam === true" class="alerta clearfix">
          Ya escribiste este mismo texto.
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import fire from "./../fire";
export default {
  name: "Chat",
  data() {
    return {
      userName: "",
      nombreUsado: false,
      name: null,
      mensaje: "",
      messages: [],
      loading: true,
      limit: 300,
      enviados: [],
      spam: false,
      grocerias: [
        "puta",
        "puto",
        "marica",
        "tula",
        "corneta",
        "callampa",
        "pico",
        "p1c0",
        "pichula",
        "pixula",
        "mierda",
        "mi3erda",
        "maraco",
        "culiao",
        "kuliao",
        "ahueonao",
        "aweonao",
        "weon",
        "hueon",
        "ql",
        "manguaco",
        "manguako",
        "maraca",
        "piko",
        "chucha",
        "xuxa",
        "verga",
        "berga",
        "maricon",
        "fleto",
        "amariconao",
        "coño",
        "maricones",
        "ctm",
        "bastarda",
        "bastardo",
        "perra",
        "penca",
        "penka",
        "caga",
        "kaga",
        "conchetumare",
        "conchetumadre",
        "chetumere",
        "hijo e la perra",
        "hijoelaperra",
        "hijo de perra",
        "desgraciados",
        "corneteros",
        "guatón",
        "wea",
        "huawei",
        "iphone",
        "apple",
        "xiaomi"
      ]
    };
  },
  methods: {
    updateUsername() {
      this.nombreUsado = false;
      if (this.userName.length > 0) {
        if (this.userName.includes("samsung") && !this.$attrs.origen) {
          alert("No puedes usar el nombre samsung");
          this.userName = null;
        } else {
          //filtramos garabatos
          let regExpSource = this.grocerias.join("|");
          let regExpFilter = new RegExp(regExpSource, "ig");
          this.userName = this.userName.replace(regExpFilter, function() {
            return "*!@*#*";
          });
          fire
            .database()
            .ref("messages")
            .orderByChild("username")
            .equalTo(this.userName.toLowerCase())
            .once("value", snapshot => {
              if (snapshot.exists()) {
                this.nombreUsado = true;
              } else {
                console.log(snapshot);
                this.name = this.userName;
                console.log(this.userName);
                sessionStorage.setItem("userName", this.userName.toLowerCase());
                this.nombreUsado = false;
              }
            });
        }
      } else {
        this.nombreUsado = false;
      }
    },
    enviarMensaje() {
      if (this.mensaje.length > 0) {
        this.spam = false;
        let regExpSource = this.grocerias.join("|");
        let regExpFilter = new RegExp(regExpSource, "ig");
        let textoFiltrado = this.mensaje.replace(regExpFilter, function() {
          return "*!@*#*";
        });
        //se acorta el mensaje
        if (textoFiltrado.length > 350) {
          textoFiltrado = textoFiltrado.substr(0, this.limit) + "...";
        }
        if (!this.checkSpam(textoFiltrado)) {
          const message = {
            text: textoFiltrado,
            username: this.name.toLowerCase(),
            fecha: Date.now()
          };
          fire
            .database()
            .ref("messages")
            .push(message);
          this.mensaje = "";
          this.scrollBottom();
        } else {
          this.spam = true;
        }
      }
    },
    scrollBottom() {
      const chatWindow = document.getElementById("mensajes");
      let xH = chatWindow.scrollHeight;
      xH = xH + 150;
      console.log(xH);
      chatWindow.scrollTo(0, xH);
      //document.getElementById("fin").scrollIntoView(true);
    },
    checkSpam(message) {
      const countOccurrences = (arr, val) =>
        arr.reduce((a, v) => (v === val ? a + 1 : a), 0);

      if (this.enviados.length > 0) {
        let veces = countOccurrences(this.enviados, message);
        console.log(veces);
        if (veces > 1) {
          console.log("ya existe");
          return true;
        } else {
          this.enviados.push(message);
          sessionStorage.setItem("messages", JSON.stringify(this.enviados));
          console.log("no existe");
          return false;
        }
      } else {
        this.enviados.push(message);
        sessionStorage.setItem("messages", JSON.stringify(this.enviados));
        console.log("no existe");
        return false;
      }
    },
    cargaGroserias() {
      const itemsRef = fire.database().ref("palabras");
      console.log(itemsRef);
      this.loading = false;
      itemsRef.on("value", snapshot => {
        let data = snapshot.val();
        let palabrotas = [];
        Object.keys(data).forEach(key => {
          palabrotas.push(data[key].palabra);
        });
        this.grocerias = palabrotas;
      });
    },
    cargaMensajes() {
      let viewMessage = this;
      const itemsRef = fire
        .database()
        .ref("messages")
        .limitToLast(200);
      console.log(itemsRef);
      this.loading = false;
      itemsRef.on("value", snapshot => {
        let data = snapshot.val();
        let messages = [];
        Object.keys(data).forEach(key => {
          messages.push({
            id: key,
            username: data[key].username,
            text: data[key].text,
            fecha: data[key].fecha
          });
        });
        viewMessage.messages = messages;
      });
    }
  },
  mounted() {
    //cargo los mensajes
    this.cargaMensajes();
    //check username exist
    let usuario = sessionStorage.getItem("userName");
    if (usuario) {
      this.userName = usuario;
      this.name = usuario;
    }
    //se pone texto en español
    this.$moment.locale("es");

    //reviso el historial de mensajes
    if (sessionStorage.getItem("messages")) {
      this.enviados = JSON.parse(sessionStorage.getItem("messages"));
    }
    //cargo las palabras prohibidas
    this.cargaGroserias();
  },
  created() {
    this.scrollBottom();
    console.log(this.$attrs.origen);
  }
};
</script>

<style lang="scss" scoped>
.alerta {
  background: #f8d7da;
  padding: 3px 5px;
  font-size: 80%;
  font-weight: 400;
  line-height: 14px;
}
.login {
  background: #fff;
  width: 100%;
  height: 65vh;
  margin: auto;
}
.spinner-border {
  display: block;
  margin: 0 auto;
}
h3 {
  font-size: 30px;
  text-align: center;
}

.message-body {
  width: 100%;
  height: 50vh;
  overflow: auto;
  margin: auto;
  padding: 10px 0;
  font-size: 14px;
  border-bottom: 1px solid #f5f5f5;
  &::-webkit-scrollbar {
    width: 14px;
    background-color: #efefef;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #000000;
  }
}

.message-text {
  border-radius: 4px;
  padding: 0px 5px 0 0;
  font-size: 13px;
  position: relative;
  margin-left: 9px;
  &::before {
    font-family: "FontAwesome";
    content: "\f007";
    position: absolute;
    width: 10%;
    height: 20px;
    left: -8px;
    top: 7px;
    color: #aaaaaa;
  }
  &.me {
    &::before {
      color: #1428a0;
    }
    span {
      color: #1428a0;
    }
  }
  &.last {
    margin-bottom: 80px !important;
  }
  .fecha {
    font-size: 11px;
    margin: -5px 0 5px 0;
    color: #222;
    font-style: italic;
  }
}

.message {
  font-size: 13px;
  line-height: 17px;
  margin-bottom: 8px;
  overflow-wrap: break-word;
}
.mg-text {
  color: black;
  font-weight: bolder;
}

.card {
  width: 100%;
  height: 75vh;
  margin: auto;
}
.card-body {
  min-height: 50vh;
  padding: 0px !important;
  hr {
    opacity: 0;
  }
}

.box-post {
  width: 100%;
  margin: 10px auto 0 auto;
  input {
    font-size: 12px;
    width: 60%;
    display: inline-block;
  }
  textarea {
    width: 60%;
    height: 50px;
    float: left;
    font-size: 12px;
  }
  button {
    font-size: 12px;
    width: 35%;
    font-weight: bold;
    float: right;
    margin-left: 2%;
    background-color: black;
    color: white;
    text-align: center;
    border-radius: 20px;
  }
}

@media only screen and (max-width: 768px) {
  .card {
    height: 45vh;
  }
  .card-body {
    min-height: 30vh;
  }
  .message-body {
    height: 35vh;
  }
}

@media only screen and (max-width: 480px) {
  .box-post {
    textarea,
    input[type="text"] {
      width: 73%;
    }
    button {
      width: 23%;
      margin-right: 2%;
    }
  }
  .message-text {
    .mg-text {
      font-size: 12px !important;
    }
    .message {
      font-size: 11px;
    }
  }
}
</style>
