<template>
  <div>
    <div class="container dashboard">
      <menuAdmin></menuAdmin>
      <div class="row">
        <div class="col-12">
          <table class="table border rounded">
            <tr v-for="item in productos" v-bind:key="item.id">
              <td>
                <img :src="item.imagen" class="img-fluid" />
              </td>
              <td class="titulo">
                <strong>{{ item.nombre }}</strong>
              </td>
              <td>
                <!--   
                <span @click="update(item)" :class="{ 'opacity' : item.visible == false}">&#128065;</span>
              -->
                <div class="custom-control custom-switch" @click="update(item)">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    :id="item.id"
                    :checked="item.visible"
                  />
                  <label
                    class="custom-control-label"
                    :class="{ opacity: item.visible == false }"
                    >Visible</label
                  >
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import menuAdmin from "../components/MenuAdmin";
import fire from "./../fire";
export default {
  name: "AdminProductos",
  components: { menuAdmin },
  data() {
    return {
      productos: [],
      username: "",
      password: "",
      logged: false,
      error: false,
      uid: null
    };
  },
  methods: {
    update(item) {
      let ver = null;
      console.log(item);
      if (item.visible == true) {
        ver = false;
      } else {
        ver = true;
      }
      fire
        .database()
        .ref("productos/" + item.id)
        .update(
          {
            visible: ver
          },
          function(error) {
            if (error) {
              console.log("error al guardar");
            } else {
              console.log("guardado");
            }
          }
        );
    }
  },
  mounted() {
    let viewMessage = this;
    const itemsRef = fire.database().ref("productos");
    console.log(itemsRef);
    itemsRef.on("value", snapshot => {
      let data = snapshot.val();
      let productos = [];
      Object.keys(data).forEach(key => {
        productos.push({
          id: key,
          nombre: data[key].nombre,
          url: data[key].url,
          visible: data[key].visible,
          imagen: data[key].imagen,
          orden: data[key].orden
        });
      });
      viewMessage.productos = productos.sort((a, b) =>
        a.orden > b.orden ? -1 : Number(a.orden < b.orden)
      );
    });
  }
};
</script>

<style lang="scss">
img {
  &.imagen {
    width: 100px;
    height: auto;
  }
}
table td{
  vertical-align: middle;
}
.titulo{
  font-size: 1.1em;
}
</style>
