<template>
  <div>
    <div class="container mb-5">
      <div class="row">
        <div class="col-12 text-center">
          <h1>{{titulo}}</h1>
        </div>
      </div>
      <div class="row mt-lg-3 mt-md-3 ">
        <div class="col-sm-12 video p-0" :class="chat? 'col-md-8 col-lg-9':'col-md-12 col-lg-12'">
          <Video :videoUrl="videoUrl"></Video>
        </div>
        <div class="col-sm-12 col-md-4 col-lg-3 pr-0 chat" v-if="(ancho > 480) && (chat)">
          <Chat :origen="origen" ></Chat>
        </div>
        <div class="col-12 order-lg-12 productos">
          <Productos></Productos>
        </div>
        <div class="col-sm-12 col-md-4 col-lg-3 pr-0 chat" v-if="(ancho <= 480) && (chat)">
          <Chat :origen="origen"></Chat>
        </div>
      </div>

      <div class="row mb-3" v-if="contador">
        <div class="col-12 p-0">
          <Timer :fecha="fecha"></Timer>
        </div>
      </div>
      <Landing></Landing>
      <div class="row mt-lg-3 mt-md-3 ">
        <div class="col-12 p-0">
          <tips></tips>
        </div>
      </div>
    </div>
    <Modal v-if="showModal" :data="modalData"></Modal>
    <Footer></Footer>
    <div class="overlay" v-if="overlay"></div>
  </div>
</template>

<script>
import fire from "./../fire";
import Chat from "../components/Chat";
import Video from "../components/Video";
import Productos from "../components/Productos";
import Landing from "../components/Landing";
import Tips from "../components/Tips";
import Modal from "../components/Modal";
import Timer from "../components/Timer";
import Footer from "../components/Footer";

export default {
  name: "Home",
  // eslint-disable-next-line vue/no-unused-components
  components: { Chat, Video, Productos, Landing, Tips, Modal, Timer, Footer },
  data() {
    return {
      ancho: 0,
      origen: false,
      showModal: false,
      ofertas: true,
      contador: false,
      chat: false,
      videoUrl: "",
      overlay: false,
      titulo:"",
      fecha: new Date("2021/02/01 22:30:00"),
      modalData: [],
      config: []
    };
  },
  methods: {
    cerrar() {
      console.log("cerrar");
      this.showModal = false;
      this.overlay = false;
    },
    mostrarModal(data) {
      console.log(data);
      this.modalData = data;
      this.showModal = true;
      this.overlay = true;
    },
    checkCurrentTime() {
      let now = new Date();
      if (now > this.fecha) {
        //this.chat = true;
      }
    }
  },
  mounted() {
    this.ancho = screen.width;
    console.log(this.ancho);
    console.log(this.$route.query.origen);
    let ref = this;
    const itemsRef = fire.database().ref("config");
    console.log(itemsRef);
    itemsRef.on("value", snapshot => {
      let data = snapshot.val();
      console.log(data);
      if (data.chat === true) {
        ref.chat = true;
      } else {
        ref.chat = false;
      }
      ref.videoUrl = data.video;
      ref.titulo = data.titulo;
      ref.contador = data.contador;
      ref.fecha = data.fecha;
    });
  },
  created() {
    if (this.$route.query.origen) {
      this.origen = true;
    }
  }
};
</script>

<style lang="scss">
.video {
  position: relative;
  height: 65vh;
  width: 100% !important;
  padding: 0;
  margin: 0;
  overflow: hidden;
  background: black;
}

.overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

@media only screen and (max-width: 800px) {
  .video,
  .chat {
    height: 45vh;
  }
}

@media only screen and (max-width: 480px) {
  .video {
    height: auto;
    min-height: 32vh;
    width: 100%;
  }
  .chat {
    margin-bottom: 20px;
    height: 40vh;
  }
  h1 {
    font-size: 2em !important;
  }
  h2 {
    font-size: 1.5em !important;
  }
}
</style>
