<template>
  <div class="container">
    <menuAdmin></menuAdmin>
    <div class="row">
      <div class="input-group">
        <input
          type="text"
          class="form-control"
          placeholder="Buscar"
          v-model="texto"
        />
        <div class="input-group-append">
          <button class="btn btn-secondary" type="button" @click="buscar()">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
    </div>
    <div class="spinner-border" role="status" v-if="loading === true">
      <span class="sr-only">cargando...</span>
    </div>
    <div class="row">
      <div class="col-12 m-0 p-0">
        <table border="1" class="table">
          <tr v-for="message in messages" :key="message.id">
            <td>
              <strong class="mg-text text-capitalize">{{
                message.username
              }}</strong>
              <p class="message pt-1">{{ message.text }}</p>
              <div class="fecha">
                <small>{{ $moment(message.fecha).fromNow() }}</small>
              </div>
            </td>
            <td>
              <div
                class="custom-control custom-switch"
                @click="update(message)"
              >
                <input
                  type="checkbox"
                  class="custom-control-input"
                  :id="message.id"
                  :checked="message.pantalla"
                />
                <label
                  class="custom-control-label"
                  :class="{ opacity: message.pantalla == false }"
                  ><i class="fa fa-television" aria-hidden="true"></i>
                </label>
              </div>
            </td>
            <td>
              <button
                type="button"
                class="btn btn-danger"
                @click="elimina(message.id)"
              >
                Borrar <i class="fa fa-trash-o" aria-hidden="true"></i>
              </button>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import fire from "./../fire";
import menuAdmin from "../components/MenuAdmin";
export default {
  name: "AdminChat",
  components: { menuAdmin },
  // eslint-disable-next-line vue/no-unused-components
  data() {
    return {
      messages: [],
      loading: false,
      texto: ""
    };
  },
  methods: {
    elimina(id) {
      let mensaje = fire
        .database()
        .ref("messages")
        .child(id);
      console.log(mensaje);
      let respuesta = confirm("Deseas eliminar este mensaje?");
      if (respuesta == true) {
        mensaje.remove();
      }
    },
    update(item) {
      let ver = null;
      console.log(item);
      if (item.pantalla == true) {
        ver = false;
      } else {
        ver = true;
      }
      fire
        .database()
        .ref("messages/" + item.id)
        .update(
          {
            pantalla: ver
          },
          function(error) {
            if (error) {
              console.log("The write failed");
            } else {
              console.log("Data saved successfully!");
            }
          }
        );
    },
    buscar() {
      this.loading = true;
      this.messages = [];
      if (this.texto.length > 0) {
        let viewMessage = this;
        const itemsRef = fire.database().ref("messages");
        console.log(itemsRef);
        itemsRef.on("value", snapshot => {
          let data = snapshot.val();
          let messages = [];
          Object.keys(data).forEach(key => {
            if (data[key].text.includes(viewMessage.texto)) {
              messages.push({
                id: key,
                username: data[key].username,
                text: data[key].text,
                fecha: data[key].fecha,
                pantalla: data[key].pantalla
              });
            }
          });
          viewMessage.messages = messages;
          viewMessage.loading = false;
        });
      }
    }
  },
  mounted() {
    this.$moment.locale("es");
  }
};
</script>

<style lang="scss">
.table {
  border-color: #ccc;
  tr {
    &:nth-child(even) {
      background: #f4f4f4;
    }
    &:nth-child(odd) {
      background: #fff;
    }
  }
  .message {
    word-break: break-all;
  }
  .fecha {
    font-style: italic;
    color: #ccc;
  }
}
</style>
