<template>
  <div v-if="productos.length > 0">
    <div class="productos_list row d-flex align-items-center justify-content-center">
      <div
        class="producto col-lg-6 col-md-8 col-sm-12 "
        v-for="item in productos"
        v-bind:key="item.id"
        :class="{ 'd-none': item.visible == false }"
      >
        <div>
          <a @click="trackProductos(item)" class="wrap">
            <img :src="item.imagen" class="img-fluid" />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import fire from "./../fire";
export default {
  name: "Productos",
  data() {
    return {
      productos: []
    };
  },
  methods: {
    trackProductos(item) {
      fire.analytics().logEvent(item.nombre);
      this.$parent.mostrarModal(item);
    }
  },
  mounted() {
    let viewMessage = this;
    const itemsRef = fire.database().ref("productos").orderByChild('orden');
    console.log(itemsRef);
    itemsRef.on("value", snapshot => {
      let data = snapshot.val();
      let productos = [];
      Object.keys(data).forEach(key => {
        productos.push({
          id: key,
          nombre: data[key].nombre,
          url: data[key].url,
          visible: data[key].visible,
          imagen: data[key].imagen,
          orden: data[key].orden
        });
      });
      viewMessage.productos = productos.sort((a, b) => a.orden < b.orden ? - 1 : Number(a.orden > b.orden))
    });
  }
};
</script>

<style lang="scss">
.productos_list {
  padding: 0;
  .producto {
    font-size: 14px;
    margin: 0;
    padding: 5px 10px 5px 0;
    a.wrap {
      display: flex;
      padding: 5px;
      color: #666;
      overflow: hidden;
      align-items: center;
      height: auto;
      transition: transform 0.2s;
      cursor: pointer;
      &:hover {
        transform: scale(1.05);
      }
      .texto {
        width: 62%;
        font-size: 13px;
        line-height: 17px;
        margin-left: 1%;
      }
      .foto {
        width: 35%;
        height: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }
  }
  .btn-comprar {
    background: #007bff;
    color: #fff;
    border: none;
    border-radius: 15px;
    padding: 5px 10px;
    font-size: 12px;
    display: block;
    width: 100px;
    margin-top: 5px;
    &:hover {
      background: #005580;
    }
    .fa {
      color: #fff;
      margin-left: 5px;
    }
  }
  .boton-chico {
    font-size: 12px;
    border-radius: 15px;
    padding: 5px 10px;
  }
  .old {
    text-decoration: line-through;
  }
}

@media only screen and (max-width: 480px) {
  .productos_list {
    padding-top: 10px;
    min-height: inherit;
    .col-sm-6{
      width: 50% !important;
    }
    .col-sm-4{
      width: 33.33333% !important;
    }
    .producto {
      margin-bottom: 5px;
      padding: 5px;
      .texto {
        font-size: 11px !important;
        line-height: 15px !important;
      }
      .btn-comprar {
        font-size: 11px;
        padding: 5px;
        width: 80px;
      }
    }
  }
}
</style>
